import React from "react";
import 'bootstrap/dist/css/bootstrap.min.css';
import Navigation from "./Navigation/Nav";
import Knob from "./Button/Knob";
import store from "../../store";
import s from './Header.module.css'
import imgfc from "./img/facebook.png"
import instagram from "./img/instagram.png"
import twitter from "./img/twitter.png"
import vector from "./img/Vector.png"
import downbtn from "./img/down-btn.png"
import Burger from "./Burger/Burger";


const Header = () => {
	return (<div id="Header" className={s.bg}>

		<Navigation links={store.links}/>
		<Burger />
		<div className={s.vector}>
			<img src={ vector } alt=""/>
		</div>
		{/*_____________________________________*/}
		<div className={s.menu}>
			<div className={s.data}>
				<p>Mon - Fri: 8PM - 10PM, Sat - Sun: 8PM - 3AM</p>
			</div>
			{/*_____________________________________*/}
			<div className={s.center}>
				<div>
					<p>RESTAURANT</p>
					<p className={s.hung}>hungry people</p>
				</div>

				<div className={s.knopH}>
					<Knob />
				</div>
			</div>
			{/*_____________________________________*/}
			<div className={s.dlp}>
				<ul className={s.linkPage}>
					<li>
						<a href="https://uk-ua.facebook.com/" target="_blank"><img src={ imgfc } alt="facebook"/></a>
					</li>
					<li>
						<a href="https://twitter.com/?lang=ru" target="_blank"><img src={twitter} alt="twitter"/></a>
					</li>
					<li>
						<a href="https://www.instagram.com/" target="_blank"><img src={instagram} alt="instagram"/></a>
					</li>
				</ul>
			</div>
			<div className={s.downBtn}>
				<a href="#about"><img src={downbtn} alt="downBTN"/></a>
			</div>
		</div>
	</div>);
}

export default Header;
