import React from "react";
import cook from "./img/Rectangle.png"
import abYell from "./img/about-yellow (1).png"
import s from "./Team.module.css"

const Team = () => {
	return (<div id="Team" className={s.bg}>
			<div className={s.team}>
				<h5>our team</h5>
			</div>
			<div className={s.center}>
				<div className={s.imgs}>
					<img src={ cook } alt="cook"/>
					<div className={s.abYell}>
						<img src={ abYell} alt="abYell"/>
					</div>
				</div>

				<div className={s.text}>
					<h2>MASTER CHEF</h2>
					<p>Lorem ipsum dolor sit amet, consectetur adipiscing elit.
						Duis at velit
						maximus, molestie est a, tempor magna.</p>
					<p>Integer ullamcorper neque eu purus euismod,
						ac faucibus mauris posuere.
						Morbi non ultrices ligula. Sed dictum,
						enim sed ullamcorper feugiat,
						dui odio vehicula eros, a sollicitudin
						lorem quam nec sem. Mauris tincidunt
						feugiat diam convallis pharetra.
						Nulla facilisis semper laoreet.</p>
				</div>
			</div>
		</div>
	)
}

export default Team;

