import React from "react";
import s from "./PrivateEvents.module.css"
import l from "./img/PriEL.png"
import r from "./img/PriER.png"


const PrivateEv = () => {
	return (<div id="PrivateEV" className={s.bg}>
			<div className={s.header1}>
				<h5>PRIVATE EVENTS</h5>
			</div>
			<div className={s.content1}>
				<img src={ l } alt="imgL"/>
				<img src={ r } alt="imgR"/>
			</div>
			<div className={s.footer1}>
				<p>For private events please call: <b>+40 729 131 637/+40 726 458 782</b> or use the contact form.</p>
			</div>
		</div>
	)
}

export default PrivateEv;

