import React from "react";
import aboutImg from "./img/about-img (1).png"
import yellow from "./img/about-yellow (1).png"
import s from "./Book.module.css"
import 'bootstrap/dist/css/bootstrap.min.css';
import { Button, Container, Row, Col,  } from 'react-bootstrap';
import Example from "./datapicker/Datepicker";
import ResponsiveTimePickers from "./timepicker/Timepicker";



const Book = () => {
	const nameRef = React.createRef()
	const logName =(e) => {
		console.log(nameRef.current.value);
	}
	return (<div id="Book">
			<div className={s.center}>
				<div className={s.center1}>
					<h5>book a table</h5>
					<Container className={s.myCont}>
						<Row className={s.myRow}>
							<Col className={s.mycol}>
								<input onChange={logName} type="text" className="form-control" placeholder="Name"
									   aria-label="Name" ref={nameRef}/>
							</Col>
							<Col className={s.mycol}>
								<input type="text" className="form-control" placeholder="Email" aria-label="Email"/>
							</Col>
						</Row>
						<Row className={s.myRow}>
							<Col className={s.mycol}>
								<input type="text" className="form-control" placeholder="Phone"
									   aria-label="Phone"/>
							</Col>
							<Col className={s.mycol}>
								<select className="form-select" aria-label="Default select example">
									<option>People</option>
									<option value="1">1</option>
									<option value="2">2</option>
									<option value="3">3</option>
									<option value="3">4</option>
								</select>
							</Col>
						</Row>
						<Row className={s.myRow}>
							<Col className={s.mycol}>
								<div className="form-group">
									<div className='input-group date' id='datetimepicker1'>
										<Example/>
									</div>
								</div>
							</Col>
							<Col className="col-6">
								<div className={s.timeCol}>
								<ResponsiveTimePickers />
								</div>
							</Col>
						</Row>
						<Row className={s.myRow}>
							<Col className="d-grid gap-2 col-12 mt-2 ">
								<Button className={s.knop}>BOOK NOW</Button>
							</Col>
						</Row>
					</Container>
				</div>
				<div className={s.center2}>
					<img src={ aboutImg } alt="about"/>
					<div>
						<img className={ s.yelBor } src={ yellow } alt="yellow"/>
					</div>
				</div>
			</div>
			<div className={s.bookFooter}>
				<p>Mon - Fri: <b>8PM - 10PM</b>, Sat - Sun: <b>8PM - 3AM</b>, Phone: <b>+40 729 131 637/+40 726 458 782</b></p>
			</div>
		</div>
	)
}

export default Book;
