import React from "react";
import { Button, Container, Row, Col } from 'react-bootstrap';
import b from "./Knob.module.css"


const Knob = () => {
	return (<Container className={b.container}>
		<Row className={b.myrow}>
			<Col className={b.mycol1}>
				<Button  className={b.knop1} href="#Book">BOOK TABLE</Button>
			</Col>
			<Col className={b.mycol2}>
				<Button className={b.knop2}>EXPLORE</Button>
			</Col>
		</Row>
	</Container>);
}

export default Knob;
