import ReactDOM from "react-dom/client";
import {
	BrowserRouter,
	Routes,
	Route,
	Link
} from "react-router-dom";
// import your route components too
import Beer from "./pages/Beer";
import Soupe from "./pages/Soupe";
import Pizza from "./pages/Pizza";
import Pasta from "./pages/Pasta";
import Desert from "./pages/Desert";
import Wine from "./pages/Wine";
import Drinks from "./pages/Drinks";

/*Styles*/
import s from './Menu.module.css'


const Menu = () => {
	return (<div id="Menu" className={s.contentMenu}>
			<div className={s.title}>
				<h5>DELICIOUS MENU</h5>
				<p>Lorem ipsum dolor sit amet, consectetur adipiscing elit. Duis at velit maximus, molestie est a, tempor magna.</p>
			</div>
			<BrowserRouter>
				<div>
					<nav>
						<ul className={s.menuList}>
							<li><Link className={s.item} to="Soupe">Soupe</Link></li>
							<li><Link className={s.item} to="Pizza">Pizza</Link></li>
							<li><Link className={s.item} to="Pasta">Pasta</Link></li>
							<li><Link className={s.item} to="Desert">Desert</Link></li>
							<li><Link className={s.item} to="Wine">Wine</Link></li>
							<li><Link className={s.item} to="Beer">Beer</Link></li>
							<li><Link className={s.item7} to="Drinks">Drinks</Link></li>
						</ul>
					</nav>
					<Routes>
						<Route path="/Soupe" element={<Soupe/>}/>
						<Route path="/Pizza" element={<Pizza/>}/>
						<Route path="/Pasta" element={<Pasta/>}/>
						<Route path="/Desert" element={<Desert/>}/>
						<Route path="/Wine" element={<Wine/>}/>
						<Route path="/Beer" element={<Beer/>}/>
						<Route path="/Drinks" element={<Drinks/>}/>
					</Routes>
				</div>
			</BrowserRouter>
		</div>
	);
}


export default Menu