import React from "react";
import i1 from "./img/1.jpg"
import i2 from "./img/2.jpg"
import i3 from "./img/3.jpg"
import i4 from "./img/4.jpg"
import s from "./Gallery.module.css"
import Fancybox from "./Fancybox";

const PrivateEv = () => {
	return (<div id="Gallery" className={s.gall}>
			<img data-fancybox="gallery" src={ i1 } alt=""/>
			<img data-fancybox="gallery" src={ i2 } alt=""/>
			<img data-fancybox="gallery" src={ i3} alt=""/>
			<img data-fancybox="gallery" src={ i4 } alt=""/>
			<Fancybox options={{ infinite: false }}>
			</Fancybox>
		</div>
	)
};

export default PrivateEv;

