import React from "react";
import s from "./Burger.module.css"

class Burger extends React.Component {
    constructor(props) {
        super(props)
            /*Состояние меню - закрыто*/
        this.state = {
            opened: false
        }
    }

    toggleOpenedStatus = () => {
        this.setState({
            opened: !this.state.opened
        }/*(state)=>{
            return {
                opened: !state.opened
            }
        }*/)
    }

    render() {
       /* console.log(this)*/
        const classNamesBtn = s.menu_btn + ' ' + (this.state.opened ? s.active : '')
        const classNamesMenu = s.menu_box + ' ' + (this.state.opened ? s.opened : '')

        return <div className={s.burger_menu}>
            <div className={classNamesBtn} onClick={this.toggleOpenedStatus}>
                <span></span>
            </div>

            <ul className={classNamesMenu}>
                <li><a className={s.menu_item} href="#Header" onClick={this.toggleOpenedStatus}>Home</a></li>
                <li><a className={s.menu_item} href="#about" onClick={this.toggleOpenedStatus}>ABOUT</a></li>
                <li><a className={s.menu_item} href="#Team" onClick={this.toggleOpenedStatus}>TEAM</a></li>
                <li><a className={s.menu_item} href="#Book" onClick={this.toggleOpenedStatus}>BOOKING</a></li>
                <li><a className={s.menu_item} href="#Specialities" onClick={this.toggleOpenedStatus}>MENU</a></li>
                <li><a className={s.menu_item} href="#Gallery" onClick={this.toggleOpenedStatus}>GALLERY</a></li>
                <li><a className={s.menu_item} href="#PrivateEV" onClick={this.toggleOpenedStatus}>EVENTS</a></li>
                <li><a className={s.menu_item} href="#Contact" onClick={this.toggleOpenedStatus}>CONTACT</a></li>
            </ul>
        </div>

    }
}
